body {
  margin: 0;
  padding: 0;
}


.wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

h1 {
  font-size: 17vh;
  text-align: center;
  margin: 0;
}

.wrp {
  position: relative;
}

.red-btn {
  background: url(./img/glossy_red_button.svg) no-repeat center/cover;
  height: 32vh;
  width: 32vh;
  text-align: center;
}

.red-btn:hover {
  cursor: pointer;
  opacity: .7;
}

.btn-1, .btn-2, .btn-3 {
  display: none;
}

.buttons {
  text-align: center;
}

.btn-1-a {
  height: 32vh;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-animation-name: to-point1;
          animation-name: to-point1;
  -webkit-animation-duration: 0.99s;
          animation-duration: 0.99s;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes to-point1 {
  
  to {
      -webkit-transform: translateY(150%) translateX(-100%);
              transform: translateY(150%) translateX(-100%);
  }
}

@keyframes to-point1 {
  
  to {
      -webkit-transform: translateY(150%) translateX(-100%);
              transform: translateY(150%) translateX(-100%);
  }
}

.btn-1-a:hover {
  cursor: pointer;
  opacity: .7;
}

.btn-2-a {
  height: 32vh;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-animation-name: to-point2;
          animation-name: to-point2;
  -webkit-animation-duration: 0.99s;
          animation-duration: 0.99s;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes to-point2 {
  
  to {
      -webkit-transform: translateY(150%);
              transform: translateY(150%);
  }
}

@keyframes to-point2 {
  
  to {
      -webkit-transform: translateY(150%);
              transform: translateY(150%);
  }
}

.btn-2-a:hover {
  cursor: pointer;
  opacity: .7;
}

.btn-3-a {
  height: 32vh;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-animation-name: to-point3;
          animation-name: to-point3;
  -webkit-animation-duration: 0.99s;
          animation-duration: 0.99s;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes to-point3 {
  
  to {
      -webkit-transform: translateY(150%) translateX(100%);
              transform: translateY(150%) translateX(100%);
  }
}

@keyframes to-point3 {
  
  to {
      -webkit-transform: translateY(150%) translateX(100%);
              transform: translateY(150%) translateX(100%);
  }
}

.btn-3-a:hover {
  cursor: pointer;
  opacity: .7;
}


